.list-headers {
    background-color: #002e25;
    color: white;
}

.banner-container {
    height: 3em;
}

.banner {
    background-color: rgb(211, 211, 112);
    font-size: larger;
    font-weight: bolder;
    color: #002e25;
}

.search-header {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.small-screen-header {
    display: none;
}

@media screen and (max-width: 767px){
    .list-headers {
        display: none;
    }

    .banner, .info {
        font-size: small;
    }

    .small-screen-header {
        display: block;
        font-size: 1em;
    }
}